.modal {
  width: 50%;

  background-color: white;
  position: fixed;
  z-index: 30;
  top: 20%;
  left: 25%;
  text-align: center;
  border-radius: 10px;
}
.overlay {
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.774);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
}
