.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.header {
  margin-top: 2rem;
}
.header button {
  padding: 1.1rem 2.1rem;
  border-radius: 8px;
  border: none;
  outline: none;
  font-size: 1.2rem;
  font-weight: bold;
  background-color: #fff7ae;
  cursor: pointer;
  position: relative;
}
.header button:hover {
  background-color: #f6d167;
}
.header button::after {
  content: "";
  width: 0%;
  height: 0.4rem;
  background-color: #df2e2e;
  left: 0;
  bottom: 0;
  position: absolute;
  opacity: 0.6;
  transition: all 0.2s ease-in-out;
}
.header button:hover::after {
  content: "";
  width: 100%;

  background-color: #df2e2e;
  position: absolute;
  left: 0;
  bottom: 0;
}
.main p{
  color:white;
  font-weight: bold;
}
