.movie {
  background-color: #fff7ae;

  padding: 1.4rem 1rem;
  width: 70%;
  margin: 1rem auto 0;
  border-radius: 10px;
  cursor: pointer;
}
.movie h1 {
  color: #df2e2e;
  margin-bottom: 1rem;
}
.movie span {
  color: #970303;
  font-size: 1rem;
  margin-left: 1.5rem;
}
.movie p {
  font-size: 1.3rem;
  font-weight: 500;
  transition: all 0.4s ease-in-out;
}
.movie:hover {
  background-color: #f6d167;
  transform: translateY(3px);
}
