.movieCard{
  padding:4rem 1rem 2rem;
  background-color: #DF2E2E;  
  border-radius: 10px;
color:wheat     
}
.movieCard h1{
    font-size: 2.1rem;
    margin-bottom: 10px;
    font-weight:800;
}
.movieCard h3{
    margin-bottom: 15px;
}
.movieCard p{
font-size: 1.4rem;
font-weight: 600;
margin-bottom: 20px;
}
.movieCard button{
    margin-top: 20px;
    padding:1rem 2rem;
    font-weight: 600;
    background-color: #297F87;
    border-radius: 8px;
    outline: none;
    border: none;
    cursor: pointer;
    position: relative;
   
}
.movieCard button:hover{
    background-color: teal;
    color:white
}
.movieCard button::after{
    content: "";
    width:0%;
    height: 5px;
    background-color: thistle;
    position: absolute;
    left: 0;
    bottom: 0;
    transition: all 0.4s ease-in-out;
    opacity: 0.5;

}
.movieCard button:hover::after{
    content: "";
    width: 100%;
   
    background-color: thistle;
    position: absolute;
    left: 0;
    bottom: 0;

}